/*------------------------------------*/
/*  Menu Header Logo and Menu
/*------------------------------------*/
#menu-top .phone {
  display: flex;
  justify-content: center;
  font-size: 1.5em;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
}
#menu-top .phone img {
  filter: invert(0) !important;
}
@media (min-width: 766px) {
  #menu-top .phone img {
    min-width: 20px !important;
  }
}
@media (max-width: 766px) {
  #menu-top .phone {
    min-width: 100%;
  }
}
#menu-top a :hover {
  opacity: 0.8;
}
@media (max-width: 766px) {
  #menu-top {
    right: 0;
    left: 0;
    top: 0px;
    z-index: 100;
    animation-duration: 0.5s;
    width: 70%;
  }
  #menu-top a {
    font-size: 38px !important;
    text-align: start;
  }
  #menu-top a:hover {
    color: white !important;
    opacity: 1;
  }
}

/*------------------------------------*/
/*  NAV Colors
/*------------------------------------*/
.navbar {
  /*------------------------------------*/
  /*  Hambumger animation
  /*------------------------------------*/
}
.navbar .contactUs {
  border: 1px solid rgba(0, 0, 0, 0) !important;
}
@media (max-width: 766px) {
  .navbar .contactUs {
    background-color: transparent !important;
  }
}
.navbar img {
  max-width: initial;
  min-width: initial;
}
.navbar a {
  font-weight: 400 !important;
}
.navbar .logo img {
  height: 15px !important;
}
@media (min-width: 766px) {
  .navbar a {
    font-size: 0.75rem !important;
    margin: 0 10px !important;
  }
  .navbar .logo img {
    max-width: 150px;
  }
}
@media (min-width: 992px) {
  .navbar a {
    font-size: 1rem !important;
  }
}
@media (max-width: 766px) {
  .navbar {
    top: 0px;
    right: 0px;
    width: 100%;
    background: black;
    z-index: 9999;
  }
  .navbar a {
    overflow: hidden;
  }
  .navbar .menu-item {
    display: flex;
    min-width: 100%;
    height: 15vh;
    margin: 0px;
    padding: 0px;
  }
  .navbar .menu-item a {
    height: 100%;
    min-width: 100%;
    margin: 0px !important;
    justify-content: flex-start;
    display: flex !important;
    align-items: center;
    color: #b5b5b8;
    text-align: left;
  }
  .navbar.fixed-top {
    box-shadow: none !important;
  }
  .navbar .btn-cta {
    width: auto;
  }
}
@media (max-width: 766px) and (max-width: 766px) {
  .navbar .btn-cta {
    width: 70%;
    margin: auto;
    margin-top: auto;
    padding: 3rem;
    margin-top: 2rem;
  }
}
@media (max-width: 766px) {
  .navbar #navbarCollapse {
    padding-top: 2rem;
    margin-left: 0rem;
    padding: 15px !important;
    min-width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-basis: initial !important;
    flex-grow: initial !important;
  }
  .navbar #navbarCollapse .navbar-wrap {
    height: 100vh;
  }
  .navbar #navbarCollapse .container {
    display: flex;
    flex-direction: column;
    align-items: center !important;
    justify-content: center;
  }
  .navbar #navbarCollapse .phoneNumber {
    display: none;
  }
  .navbar #navbarCollapse .btn-cta {
    padding: 0% !important;
    margin: 0px !important;
    font-size: 38px !important;
    color: #b5b5b8 !important;
    text-align: start;
    flex-grow: initial;
    flex-basis: initial;
    justify-content: start !important;
    height: 15vh;
    width: 100%;
    position: relative !important;
  }
  .navbar #navbarCollapse .btn-cta:hover {
    color: white !important;
  }
  .navbar #navbarCollapse .btn-cta:hover:after {
    transform-origin: left top !important;
    transform: scale(1, 1) !important;
    z-index: 100;
  }
}
@media (max-width: 766px) and (max-width: 766px) {
  .navbar #navbarCollapse .btn-cta:hover:after {
    height: 5px;
  }
}
@media (max-width: 766px) {
  .navbar #navbarCollapse .btn-cta::after {
    position: absolute !important;
    left: 0;
    content: "";
    width: 100%;
    height: 2px;
    background: currentColor;
    bottom: -4px;
    transform: scale(0, 1);
    transition: transform 0.3s !important;
    transform-origin: right top !important;
  }
}
.navbar #navbarCollapse .menu-primary {
  flex-grow: initial;
  flex-basis: initial;
  width: auto;
}
.navbar #navbarCollapse .menu-primary a,
.navbar #navbarCollapse .menu-primary .active {
  text-decoration: none;
  display: inline-block;
  position: relative;
  font-family: Inter;
  font-style: normal;
  font-size: 1rem;
  margin: 0 16px;
  font-weight: 700;
  line-height: 24px;
  white-space: nowrap;
  text-transform: capitalize;
}
@media (max-width: 766px) {
  .navbar #navbarCollapse .menu-primary a,
.navbar #navbarCollapse .menu-primary .active {
    color: #b5b5b8;
    font-size: 48px;
    font-family: Inter;
    font-weight: 700;
    justify-content: left !important;
  }
}
.navbar #navbarCollapse .menu-primary a:hover:after,
.navbar #navbarCollapse .menu-primary .active:hover:after {
  transform-origin: left top;
  transform: scale(1, 1);
  z-index: 100;
}
@media (max-width: 766px) {
  .navbar #navbarCollapse .menu-primary a:hover:after,
.navbar #navbarCollapse .menu-primary .active:hover:after {
    height: 5px;
  }
}
.navbar #navbarCollapse .menu-primary a:after,
.navbar #navbarCollapse .menu-primary .active:after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: currentColor;
  bottom: -4px;
  transform: scale(0, 1);
  transition: transform 0.3s;
  transform-origin: right top;
}
.navbar #navbarCollapse .menu-primary .active:after {
  transform-origin: left top;
  transform: scale(1, 1) !important;
  z-index: 100;
}
@media (max-width: 766px) {
  .navbar #navbarCollapse .menu-primary .active:after {
    height: 5px;
  }
}
.navbar.fixed-top {
  top: 0;
  position: fixed;
  animation-duration: 0.5s;
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.1);
  background-color: black;
  animation-name: navbar;
}
.navbar .bar {
  width: 35px;
  height: 2px;
  background-color: white;
  margin: 8px 0;
  transition: 0.4s;
}
.navbar :not(.collapsed) .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}
.navbar :not(.collapsed) .bar2 {
  opacity: 0;
}
.navbar :not(.collapsed) .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}
.navbar-dark {
  color: white;
}
.navbar-dark a {
  color: white;
}
.navbar-dark a:hover {
  color: white;
}
.navbar-light {
  color: black;
}
.navbar-light a {
  color: black;
}
.navbar-light a:hover {
  color: black;
}
.navbar-light a img {
  filter: invert(1);
}

